<template>
  <va-card :title="$t('tables.labelinvoices')" class="w-100">

    <div class="row ml-0">

      <div class="flex xs12 sm4">
        <label class="input-label"> {{$t('forms.search')}} </label>
        <va-input v-model="searchText" :placeholder="$t('forms.search')" />
      </div>
      <va-button
        icon="fa fa-search"
        flat
        slot="actions"
        color="success"
        class="mr-0"
        @click="search"
      />
      <va-button
        icon="fa fa-trash"
        flat
        slot="actions"
        color="danger"
        class="mr-0"
        @click="deletesearch"
      />
      <div class="flex xs12 md3">
        <label class="input-label"> {{$t('placeholder.invType')}} </label>

        <va-select
          v-model="invtypeString"
          :label="$t('placeholder.invType')"
          :options="invOptions"
          noClear
        />
      </div>
    </div>
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      :no-pagination = "searchBool"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        {{props.rowIndex + 1}}
      </template>
      <template slot="date" slot-scope="props">
        {{ formatDate(props.rowData.date)}}
      </template>

      <template slot="is_Cash" slot-scope="props">
        {{props.rowData.is_Cash  ? '&#10004;': '&#10008;' }}
      </template>
      <template slot="iS_Paid" slot-scope="props">
        {{props.rowData.iS_Paid  ? '&#10004;': '&#10008;' }}
      </template>
      <template slot="inv_dueDate" slot-scope="props">
        {{ formatDate(props.rowData.inv_dueDate)}}
      </template>
      <template slot="customers" slot-scope="props">
        {{props.rowData.customers === null ? '-----': props.rowData.customers.name }}
      </template>
      <template slot="employees" slot-scope="props">
        {{props.rowData.employees === null ? '-----': props.rowData.employees.name }}
      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="gray" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.edit') }}
        </va-button>
      </template>
    </va-data-table>

    <va-modal v-model="deleteModal" no-outside-dismiss
      :title=" $t('generic.deleteItemTitle') "
      :okText=" $t('modal.delete') "
      :cancelText=" $t('modal.cancel') "
      :message=" $t('generic.deleteItemMsg') "
      @ok="okDeletefun()"
      @cancel="cancleDeletefun()">
    </va-modal>

    <va-modal v-model="createModal" no-outside-dismiss
      size="large"
      :title="$t(modelTitle)"
      hide-default-actions >
      <form @submit.prevent="editModal? okEditfun()  :okCreatefun()">

        <div class="d-flex justify--center mt-3">
          <va-button type="button" color="gray" flat @click="editModal? cancleEditfun() : cancleCreatefun()">
            {{ $t('modal.cancel') }}
          </va-button>
          <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t(modelOktext) }}</va-button>
        </div>
      </form>

    </va-modal>
  </va-card>

</template>

<script>
import invoicesService from '../../../../services/BIll/BillService'

export default {
  data () {
    return {
      modelTitle: '',
      modelOktext: '',
      stopBtn: false,
      perPage: 10,
      totalPages: 0,
      inv_type: 1,
      invtypeString: 'sell',
      invOptions: ['sell', 'buy', 'RSaleInv', 'RbuyInv', 'PreviewInv'],
      OrderBy: null,
      dataTable: [],
      deleteModal: false,
      editModal: false,
      createModal: false,
      loading: true,
      opjectBeforeEdit: {},
      currentobject: {},
      fKCity: {
        value: '',
        options: [],
      },
      searchText: '',
      searchBool: false,
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',

      }, {
        name: '__slot:date',
        title: this.$t('tables.headings.date'),
      },
      {
        name: 'total_Price',
        title: this.$t('tables.headings.total_Price'),
      },
      {
        name: '__slot:is_Cash',
        title: this.$t('tables.headings.is_Cash'),
      },
      {
        name: '__slot:iS_Paid',
        title: this.$t('tables.headings.iS_Paid'),
      },
      {
        name: 'paid',
        title: this.$t('tables.headings.paid'),
      },
      {
        name: 'tax',
        title: this.$t('tables.headings.tax'),
      }, {
        name: '__slot:inv_dueDate',
        title: this.$t('tables.headings.inv_dueDate'),
      }, {
        name: '__slot:customers',
        title: this.$t('tables.headings.customers'),
      }, {
        name: '__slot:employees',
        title: this.$t('tables.headings.employees'),
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
  },
  watch: {
    invtypeString: function () {
      switch (this.invtypeString) {
        case 'sell':
          this.inv_type = 1
          break
        case 'buy':
          this.inv_type = 2
          break
        case 'RSaleInv':
          this.inv_type = 3
          break
        case 'RbuyInv':
          this.inv_type = 4
          break
        case 'PreviewInv':
          this.inv_type = 5
          break
        default:
          this.inv_type = 1
          break
      }
      this.readItems()
    },
  },
  created () {
    this.readItems()
  },
  methods: {
    /*  api get */
    readItems (page = 1) {
      this.loading = true
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
        OrderBy: this.OrderBy,
        inv_type: this.inv_type,
        Fk_CompanyActivity: 1,
      }
      invoicesService.getAllPagnation(params)
        .then(response => {
          this.loading = false
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    formatDate (dateString, FullDate = true) {
      if (FullDate) {
        const options = { weekday: 'short', year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      } else {
        const options = { year: 'numeric', month: 'numeric', day: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      }
    },
    search () {
      this.loading = true

      const params = {
        Text: this.searchText,
      }
      this.dataTable = []
      invoicesService.search(params)
        .then(response => {
          this.dataTable = response.data
          this.totalPages = Math.ceil(this.dataTable.length / 10)
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },

    deletesearch () {
      this.searchText = ''
      this.searchBool = false
      this.readItems()
    },
    /*  end api get */
    /* cud api */
    create () {
      if (this.fKCity.value !== '') {
        this.currentobject.fK_City = this.fKCity.value.id
      }
      this.currentobject.Customer_Type ? this.currentobject.fK_Customer_Type = 2 : this.currentobject.fK_Customer_Type = 1
      invoicesService.create(this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.loading = false
          this.currentobject = {}
          this.fKCity = ''
          this.createModal = false
          this.stopBtn = false

          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.stopBtn = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.stopBtn = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    edit (user) {
      if (this.fKCity.value !== '') {
        this.currentobject.fK_City = this.fKCity.value.id
      }
      this.currentobject.Customer_Type ? this.currentobject.fK_Customer_Type = 2 : this.currentobject.fK_Customer_Type = 1

      invoicesService.update(this.currentobject.id, this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.currentobject = {}
          this.fKCity.value = ''
          this.loading = false
          this.editModal = false
          this.createModal = false
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.stopBtn = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.stopBtn = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    remove (id) {
      invoicesService.delete(id).then(response => {
        if (response.headers['x-status'] === 'true') {
          const idx = this.dataTable.findIndex(u => u.id === id)
          this.dataTable.splice(idx, 1)
          this.loading = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    /* cud api */

    /* Delete model */
    showDeleteModal (cuobject) {
      this.deleteModal = true
      this.currentobject = cuobject
      this.Productpriceloading = true
    },
    okDeletefun () {
      this.remove(this.currentobject.id)
    },
    cancleDeletefun () {
      this.loading = false
      this.stopBtn = false
    },
    /* end Delete model */

    /* edit model */
    showEditModal (cuobject) {
      this.editModal = true
      this.modelTitle = 'generic.editItemTitle'
      this.modelOktext = 'modal.edit'
      this.createModal = true
      this.fKCity.value = cuobject.city
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.currentobject = cuobject
      this.currentobject.Customer_Type = this.currentobject.fK_Customer_Type === 2
      this.loading = true
    },
    okEditfun () {
      this.stopBtn = true
      this.edit(this.currentobject)
    },
    cancleEditfun () {
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.createModal = false
      this.loading = false
      this.currentobject = {}
      this.fKCity.value = ''
      this.editModal = false
      this.stopBtn = false
    },
    /* end edit model */

    /* create model */
    showCreateModal () {
      this.modelTitle = 'generic.creatItemTitle'
      this.modelOktext = 'modal.create'
      this.createModal = true
      this.loading = true
    },
    okCreatefun () {
      this.stopBtn = true
      this.create()
    },
    cancleCreatefun () {
      this.createModal = false
      this.loading = false
      this.currentobject = {}
      this.fKCity.value = ''
      this.stopBtn = false
    },
    /* end create model */
  },
}

</script>

<style lang="scss" >
  i.fa.fa-exclamation {
    color: red !important;
  }

  .va-modal__inner {
    max-width: 100%;
  }

  .row-reverse {
    flex-direction: row-reverse !important;
  }
</style>
